<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click="decrementProduct">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>
    <label for="control-19">
      <input type="text" v-model="amount">
    </label>
    <button type="button" aria-label="Добавить один товар" @click="incrementProduct">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>

export default {
  data() {
    return {
      amount: 1,
    };
  },
  watch: {
    amount() {
      this.$emit('changeAmount', this.amount);
    },
  },
  methods: {
    incrementProduct() {
      this.amount += 1;
    },
    decrementProduct() {
      if (this.amount > 1) {
        this.amount -= 1;
      }
    },
  },
  props: ['productAmount'],
  created() {
    if (this.productAmount) {
      this.amount = this.productAmount;
    }
  },
};

</script>
